.work_section {
    max-width: 1920px;

    margin-top: 44px;
    background-color: rgb(15, 15, 15);

    img {
        height: 108px;
        margin-bottom: 60px;
        margin-top: 24px;
        margin-left: 0px;
    }

    span {
        margin-top: 44px;
        color: #4AD592;
        font-size: 96px;
        font-weight: bold;
        font-family: 'Baloo 2';
    }
}

.ml {
    margin-left: 520px;
}

.mywork_text_img {
    max-width: 1920px;
    display: flex;
    justify-content: space-around;

    p {
        color: #4AD592;
        margin-right: 290px;
        font-size: 23px;
        font-family: 'Montserrat';
        font-weight: bold;
    }

    img {
        height: 183px;
    }
}

.discription {
    width: 600px;
   
    a {
        margin-top: 32px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-decoration: none;
        color: #4AD592;
        border: 2px solid #4AD592;
        border-radius: 7px;
        height: 58px;
        width: 160px;
        background-color: #0F0F0F;
        font-size: 23px;
        font-weight: bold;
        transition: background-color 1.1s;

        &:hover {
            background-color: #4AD592;
            color: #0F0F0F;
        }
    }

    display: flex;
    flex-direction: column;
    font-size: 22px;
    font-weight: bold;
    align-items: flex-start;
}

.projecte_container {
    max-width: 1920px;
}

.project1_container {
    display: flex;
    color: #4AD592;
    justify-content: space-around;
    font-family: 'Montserrat';
    margin-top: 80px;

    img {
        height: 520px;
        width: 900px;
        transition: transform 0.7s;

        &:hover {
            transform: scale(1.2, 1.2);
        }
    }
}

.project2_container {
    display: flex;
    color: #4AD592;
    justify-content: space-around;
    margin-top: 116px;
    font-family: 'Montserrat';

    img {
        height: 500px;
        width: 690px;
        transition: transform 0.7s;

        &:hover {
            transform: scale(1.1, 1.1);
        }
    }
}

.project3_container {
    display: flex;
    color: #4AD592;
    justify-content: space-around;
    margin-top: 116px;
    font-family: 'Montserrat';

    img {
        height: 520px;
        width: 700px;
        transition: transform 0.7s;
        z-index: 999;
        margin-left: -100px;

        &:hover {
            transform: scale(1.2, 1.2);
        }
    }
}

.dflexalink {
    display: flex;
    gap: 8px;
    height: 96px;

}

.dflexalink a {
    background: linear-gradient(to top left, transparent 50%, #4AD592 50%);
    background-size: 200% 200%;
    background-position: bottom right;
    color: rgb(113, 101, 101);
    transition: all 0.25s ease;
}

.dflexalink a:hover {
    background-position: top left;
    color: whitesmoke;
    text-shadow: 1px 1px 4px black;
}

#last_project_bubble {
    height: 120px;
    width: 120px;
    position: relative;
    left: 600px;
    bottom: 90px;
}

#first_project_bubble {
    height: 400px;
    width: 400px;
    position: absolute;
    left: -330px;
    top: 3050px;
}

#red_stripe {
    color: #4AD592;
    font-size: 22px;
    animation: pulse 2s infinite;
}

#red_stripe2 {
    color: #4AD592;
    font-size: 22px;
    animation: pulse 2s infinite;
}

#red_stripe,
#red_stripe2 {
    color: #4AD592;
    font-size: 44px;
}

.projecte_container_lower {
    display: none;
}

@keyframes pulse {
    0% {
        color: #336955;
    }

    50% {
        color: #4AD592;
    }

    100% {
        color: #395133;
    }
}

